export default function validateReservation(values) {
  let errors = {};

  if (!values.nameClient) {
    errors.nameClient = "Se requiere nombre";
  }

  //Email
  if (!values.emailClient) {
    errors.emailClient = "Se require email";
  } else if (!/^[A-Z0-9._%+-]+@[A-Z09._]+\.[A-Z]{2,}$/i.test(values.emailClient)) {
    errors.emailClient = "El correo no es valido";
  }

  //
  if (!values.phoneClient) {
    errors.phoneClient = "Se requiere telefono celular";
  }

  if (!values.amountPeople) {
    errors.amountPeople = "Se require cantidad de personas";
  }

  if(!values.selectService) {
      errors.selectService = "Se requiere Servicio";
  }

  if(!values.dateSelected) {
      errors.dateSelected = "Se requiere fecha y hora";
  }

  return errors;
}
