import React from "react";
import { Grid, Typography } from "@mui/material";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";


const Gridx2 = (props) => {
  return (
    <div>
      <Grid
        container
        rowSpacing={5}
        columnSpacing={3}
        sx={{ marginBottom: "3em" }}
      >
        <Grid item xs={12} md={6} sx={{ textAlign: "center" }}>
          <LazyLoadImage effect="blur" className="imgPropuesta" src={props.img} alt="lol" />
        </Grid>
        <Grid item xs={12} md={6}>
          <div className="title-per">
            <Typography variant="h5">{props.first}</Typography>
            <Typography
              variant="h2"
              sx={{ fontWeight: "bold", color: "var(--primary)" }}
            >
              {props.second}
            </Typography>
          </div>
          <Typography variant="body1" gutterBottom sx={{ marginTop: "-5px" }}>
            <span className="highLights">{props.myHighLight}</span>
            {props.description}
          </Typography>
        </Grid>
      </Grid>

    </div>
  );
};

export default Gridx2;
