import React from 'react';
import { Box } from '@mui/system';
import { Typography } from '@mui/material';


const mutedTextColor = '#94A0AB';

const MainTitle = (props) => {
    return (
        <Box sx={{width:'100%', marginTop:'3em', marginBottom:'3em',textAlign:'center'}}>
            <Typography
                variant="h3"
                fontWeight={500}
                marginBottom="0.6em"
            >{props.textContent}
            </Typography>
            <div className="underline"></div>
            <Typography
                variant="body2"
                color={mutedTextColor}
                marginTop="0.5em"
            >
                {props.description}
            </Typography>
        </Box>
    );
};

export default MainTitle;