import "./App.css";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

//Components
import Navbar from "./components/Navbar/Navbar";
import FirstSection from "./components/FirstSection/FirstSection";
import SecondSection from "./components/SecondSection/SecondSection";
import ThirdSection from "./components/ThirdSection/ThirdSection";
import FourthSection from "./components/FourthSection/FourthSection";
import FifthSection from "./components/FifthSection/FifthSection";

function App() {
  return (
    <Router>
      <div className="AppContent">
        <header>
          <Navbar />
        </header>
        <Switch>
          <Route path ="/thirdchallenge" exact component={FirstSection} />
          <Route path="/thirdchallenge/nosotros" exact component={SecondSection} />
          <Route path="/thirdchallenge/menu" exact component={ThirdSection} />
          <Route path="/thirdchallenge/servicios" exact component={FourthSection} />
          <Route path="/thirdchallenge/contacto" exact component={FifthSection} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
