import { Container } from '@mui/material';
import React from 'react';
import Gridx2 from '../Gridx2';
import MainTitle from '../Texts/MainTitle';
import { Grid,Typography } from '@mui/material';
import {
    Card,
    CardContent,
    Avatar,
    Chip,
    Divider,
  } from "@mui/material";
  import { WorkersData } from "./WorkersData";

import './SecondSection.css';



const MyCard = () => {
    return (
      <Grid
        container
        rowSpacing={5}
        columnSpacing={3}
        alignItems="center"
        justifyContent="center"
        sx={{
          marginBottom: "4em",
        }}
      >
        {WorkersData.map((data) => {
          return (
            <React.StrictMode key={data.id}>
              <Grid item xs={12} md={4}>
                <Card
                  sx={{
                    background: "#262626",
                    minWidth: 275,
                    minHeight: 300,
                    textAlign: "center",
                  }}
                >
                  <CardContent
                      sx={{
                          marginTop:'2em'
                      }} 
                  >
                    <Avatar
                      alt={data.name}
                      src={data.img}
                      sx={{ width: 100, height: 100, margin: "auto" }}
                    />
                    <Typography
                      sx={{
                        fontSize: "1.3rem",
                        color: "white",
                        marginTop: "0.5em",
                      }}
                      color="text.primary"
                      gutterBottom
                    >
                      {data.name}
                    </Typography>
                    <Divider>
                      <Chip
                        label={data.job.toUpperCase()}
                        sx={{ 
                        color: "white", 
                        background: "#4A4A4A"
                       }}
                      />
                    </Divider>
                    <Typography variant="body2" sx={{marginTop:'1em',color:'white'}}>
                      Lorem Ipsum has been the nturies, but also the leap into electronic typesetting, remaining essentially unchanged.
                      </Typography>
                  </CardContent>
                </Card>
              </Grid>
            </React.StrictMode>
          );
        })}
      </Grid>
    );
  };

const SecondSection = () => {
    return (

        <div className="second-section">
            <Container>
            <MainTitle
            textContent="Nosotros"
            description="Contrary to atin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections"
            />
            
            <Gridx2
            first="Nuestra"
            second="Historia"
                img={require('../../assets/images/foto3@2x.png').default}
                description="It is a long established tracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like)."
            />
            <MainTitle
                textContent="Nuestro equipo"
            />
            <MyCard/>
            </Container>
        </div>
    );
};

export default SecondSection;