
export default function validateInfo(values) {

    let errors = {}

    if(!values.service) {
        errors.service = "Se requiere servicio"
    }

    //
    if(!values.subject){
        errors.subject = "Se requiere asunto"
    }

    if(!values.client){
        errors.client = "Se require nombre del cliente"
    }

    //Email
    if(!values.email){
        errors.email = "Se require email"
    } else if(!/^[A-Z0-9._%+-]+@[A-Z09._]+\.[A-Z]{2,}$/i.test(values.email)){
        errors.email = "El correo no es valido"
    }


    return errors;

}