import React from "react";
import { Grid, Modal, Typography, MenuItem } from "@mui/material";
import { Box } from "@mui/system";
import MainButton from "./MainButton";
import BookIcon from "@mui/icons-material/Book";
import { styled } from "@mui/system";
import { TextField } from "@mui/material";
import { serviceValues } from "./serviceValues";
import SendIcon from "@mui/icons-material/Send";
import json2mq from "json2mq";
import { useMediaQuery } from "@mui/material";

import { Snackbar } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import { Button } from "@mui/material";

import AdapterDateFns from "@date-io/date-fns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import MobileDateTimePicker from "@mui/lab/MobileDateTimePicker";

import useFormReservation from "./FirstSection/useFormReservation";
import validateReservation from "./FirstSection/validateReservation";

const styleDesktop = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%,-50%)",
  width: "70%",
  bgcolor: "#FAFAFA",
  boxShadow: 24,
  overFlow: "visible",
};

const styleMobile = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%,-50%)",
  width: "90%",
  bgcolor: "#FAFAFA",
  boxShadow: 24,
  overFlow: "visible",
};

const CssTextField = styled(TextField)({
  "& label.Mui-focused": {
    color: "var(--primary)",
  },
  "& .MuiInput-underline:after": {
    borderBottomColor: "green",
  },
  "& .MuiOutlinedInput-root": {
    "& fieldset": {
      borderColor: "#606060",
    },
    "&:hover fieldset": {
      borderColor: "var(--primary)",
    },
    "&.Mui-focused fieldset": {
      borderColor: "var(--primary)",
    },
  },
  "& .MuiInputLabel-root": {
    color: "rgba(10, 10, 10, 0.6)",
  },
  "& .MuiInputBase-root": {
    color: "rgba(10, 10, 10, 0.6)",
  },
  "& .MuiFormHelperText-root": {
    color: "rgba(255, 255, 255, 0.6)",
  },
});

const ModalContent = ({ submitForm }) => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [valueDate, setValueDate] = React.useState(new Date());

  const { handleChange, values, handleSubmit, errors } = useFormReservation(
    submitForm,
    validateReservation
  );

  const matches = useMediaQuery(
    json2mq({
      minWidth: 600,
    })
  );
  return (
    <div>
      <MainButton
        onclick={handleOpen}
        buttonName="HACER RESERVA"
        icon={<BookIcon />}
      />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={matches === true ? styleDesktop : styleMobile}>
          <Grid container>
            <Grid
              className="myScroll"
              item
              xs={12}
              md={7}
              sx={{
                overflowY: "scroll",
                height: "90vh",
                p: 3,
              }}
            >
              <Typography
                variant="h4"
                sx={{ color: "black", fontWeight: "bolder", fontSize: "2.6em" }}
              >
                Haz tu reserva
              </Typography>
              <Typography
                variant="h5"
                sx={{ color: "black", fontWeight: "bolder", fontSize: "1.9em" }}
              >
                Facil y rapido!
              </Typography>
              <Typography variant="caption" sx={{ color: "#505050" }}>
                Lorem ipsum dolor sit amet consectetur sumenda in odio facilis,
                distinc
              </Typography>

              <Box
                component="form"
                onSubmit={handleSubmit}
                sx={{
                  "& .MuiTextField-root": {
                    marginTop: 1,
                    marginBottom: 1,
                    width: "100%",
                  },
                }}
                autoComplete="off"
              >
                <div className="leftSide">
                  <CssTextField
                    id="nameClient"
                    placeholder="Nombre y Apellido completo"
                    label="Nombre"
                    name="nameClient"
                    value={values.nameClient}
                    onChange={handleChange}
                    error={errors.nameClient && true}
                    helperText={
                      errors.nameClient
                        ? errors.nameClient
                        : "Este campo es olbigatorio *"
                    }
                  />

                  <CssTextField
                    id="emailClient"
                    placeholder="Correo"
                    label="Correo"
                    name="emailClient"
                    value={values.emailClient}
                    onChange={handleChange}
                    error={errors.emailClient && true}
                    helperText={
                      errors.emailClient
                        ? errors.emailClient
                        : "Este campo es olbigatorio *"
                    }
                  />

                  <CssTextField
                    id="phoneClient"
                    placeholder="Numero Celular "
                    label="Telefono celular"
                    name="phoneClient"
                    type="tel"
                    value={values.phoneClient}
                    onChange={handleChange}
                    error={errors.phoneClient && true}
                    helperText={
                      errors.phoneClient
                        ? errors.phoneClient
                        : "Este campo es olbigatorio *"
                    }
                  />

                  <CssTextField
                    id="amountPeople"
                    placeholder="Cantidad de personas"
                    label="Cantidad de personas"
                    name="amountPeople"
                    type="number"
                    value={values.amountPeople}
                    onChange={handleChange}
                    error={errors.amountPeople && true}
                    helperText={
                      errors.amountPeople
                        ? errors.amountPeople
                        : "Este campo es olbigatorio *"
                    }
                  />
                  <CssTextField
                    id="selectService"
                    select
                    defaultValue={0}
                    label="Seleccionar"
                    name="selectService"
                    onChange={handleChange}
                    error={errors.selectService && true}
                    helperText={
                      errors.selectService
                        ? errors.selectService
                        : "Este campo es obligatorioo *"
                    }
                  >
                    {serviceValues.map((option) => {
                      return (
                        <MenuItem
                          disabled={option.disabled}
                          key={option.value}
                          value={option.value}
                        >
                          {option.label}
                        </MenuItem>
                      );
                    })}
                  </CssTextField>

                  <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <MobileDateTimePicker
                      label="Fecha y hora"
                      value={valueDate.toString()}
                      error={errors.dateSelected && true}
                      onChange={(newValue) => {
                        setValueDate(newValue);
                        const myNewDate = newValue.toString();
                        handleChange({
                          target: { name: "dateSelected", value: myNewDate },
                        });
                      }}
                      renderInput={(params) => <CssTextField {...params} />}
                    />
                  </LocalizationProvider>
                  <MainButton
                    buttonName="Enviar"
                    icon={<SendIcon />}
                    type="submit"
                  />
                </div>
              </Box>
            </Grid>
            <Grid
              item
              xs={12}
              md={5}
              sx={{ textAlign: "center", bgcolor: "var(--primary)" }}
            >
              <div
                className="patternReservas"
                style={{
                  backgroundRepeat: "repeat",
                  backgroundImage: `url(${
                    require("../assets/images/patterReservas.png").default
                  })`,
                }}
              ></div>
            </Grid>
          </Grid>
        </Box>
      </Modal>
    </div>
  );
};

const SuccessForm = () => {
  const [open, setOpen] = React.useState(true);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const action = (
    <Button
      endIcon={<CloseIcon />}
      sx={{ color: "white" }}
      onClick={handleClose}
    ></Button>
  );

  return (
    <Snackbar
      open={open}
      autoHideDuration={6000}
      message="Reserva realizada 👍"
      action={action}
    />
  );
};

const MyModal = () => {
  const [isSubmitted, setIsSubmitted] = React.useState(false);
  function submitForm() {
    setIsSubmitted(true);
  }
  return (
    <>
      {!isSubmitted ? (
        <ModalContent submitForm={submitForm} />
      ) : (
        <SuccessForm />
      )}
    </>
  );
};

export default MyModal;
