import { useState, useEffect } from "react";

import emailjs from "emailjs-com";

const useFormReservation = (callback, validate) => {
  const [values, setValues] = useState({
    nameClient: "",
    emailClient: "",
    phoneClient: "",
    amountPeople: "",
    selectService: "",
    dateSelected: "",
  });

  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setValues({
      ...values,
      [name]: value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (validate) {
      setErrors(validate(values));
    }
    setIsSubmitting(true);
    sendEmail();
    
  };

  const sendEmail = () => {
    const templateParams = {
      nameClient: values.nameClient,
      emailClient: values.emailClient,
      phoneClient: values.phoneClient,
      amountPeople: values.amountPeople,
      selectService: values.selectService,
      dateSelected: values.dateSelected,
    };

    emailjs
      .send(
        "service_96u7k67",
        "template_2hg7edw",
        templateParams,
        "user_q5cFAR8otIbcJgpjd8fqK"
      )
      .then(
        (response) => {
          console.log("SUCCESS!", response.status, response.text);
          
        },
        (err) => {
          console.log("FAILED...", err);
        }
      );
  };

  useEffect(() => {
    if (Object.keys(errors).length === 0 && isSubmitting) {
      if (callback) {
        callback();
      }
    }
  }, [errors, isSubmitting, callback, values]);

  return { handleChange, values, handleSubmit, errors};
};

export default useFormReservation;
