import React, { useState } from "react";
import {
  Button,
  Container,
  MenuItem,
  TextField,
  Typography,
} from "@mui/material";
import { Box, styled } from "@mui/system";
import MainTitle from "../Texts/MainTitle";
import MainButton from "../MainButton";
import SendIcon from "@mui/icons-material/Send";
import useForm from "./useForm";
import "./FifthSection.css";
import { useHistory } from "react-router";
import { serviceValues } from "../serviceValues";


import validateInfo from "./validateInfo";



const CssTextField = styled(TextField)({
  "& label.Mui-focused": {
    color: "var(--primary)",
  },
  "& .MuiInput-underline:after": {
    borderBottomColor: "green",
  },
  "& .MuiOutlinedInput-root": {
    "& fieldset": {
      borderColor: "white",
    },
    "&:hover fieldset": {
      borderColor: "var(--primary)",
    },
    "&.Mui-focused fieldset": {
      borderColor: "var(--primary)",
    },
  },
  "& .MuiInputLabel-root": {
    color: "rgba(255, 255, 255, 0.6)",
  },
  "& .MuiInputBase-root": {
    color: "rgba(255, 255, 255, 0.9)",
  },
  "& .MuiFormHelperText-root": {
    color: "rgba(255, 255, 255, 0.6)",
  },
});

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%,-50%)",
  width: '100%',
  boxShadow: 24,
  textAlign: "center",
};


const ContactFormSuccess = () => {

  let history = useHistory();

  const handleRefresh = () => {
    history.push("/thirdchallenge/")
  };

  return (
    <>
      <div className="submitPage">
        <Box sx={style}>
          <img
            loading="lazy"
            className="successImg"
            alt="myloadimg"
            src={require("../../assets/images/cutelife.gif").default}
          />
          <Typography
            variant="h4"
            sx={{ color: "white", fontWeight: "bolder", marginTop: "3em",fontSize:'2.5rem' }}
          >
            GRACIAS! 
          </Typography>
          <Typography variant="body" sx={{color:'#F4F4F4',fontSize:'1rem',fontWeight:'light'}} >
          Praesent posuere est et est accumsan lobortis. Donec eget enim metus. Curabitur sit amet dolor elit.
          </Typography>

          <Button 
          onClick={handleRefresh}
          variant="contained" sx={{
          position:'absolute',
          top:'110%',
          left:'50%',
          transform:'translate(-50%,0%)',
          background:'var(--primary)',
          "&:hover": {
            backgroundColor: "#FBFAFA",
            color:'#4A4A4A'
          },
          }}>
            VOLVER
          </Button>
        </Box>
      </div>
    </>
  );
};

const ContactForm = ({ submitForm }) => {
  const { handleChange, values, handleSubmit, errors } = useForm(
    submitForm,
    validateInfo
  );


  return (
    <div className="formContainer">
      <Box
        component="form"
        sx={{ "& .MuiTextField-root": { m: 1, width: "100%" } }}
        autoComplete="off"
        onSubmit={handleSubmit}
      >
        <div>
          <CssTextField
            id="select-service"
            select
            defaultValue={0}
            label="Select"
            onChange={handleChange}
            name="service"
            error={errors.service && true}
            helperText={
              errors.service ? errors.service : "Este campo es obligatorio *"
            }
          >
            {serviceValues.map((option) => {
              return (
                <MenuItem
                  disabled={option.disabled}
                  key={option.value}
                  value={option.value}
                >
                  {option.label}
                </MenuItem>
              );
            })}
          </CssTextField>

          <CssTextField
            id="subject"
            placeholder="Asunto"
            label="Asunto"
            name="subject"
            value={values.subject}
            onChange={handleChange}
            error={errors.subject && true}
            helperText={
              errors.subject ? errors.subject : "Este campo es obligatorio *"
            }
          />
          <CssTextField
            id="name"
            placeholder="Nombre completo"
            label="Nombre completo"
            name="client"
            value={values.client}
            onChange={handleChange}
            error={errors.client && true}
            helperText={
              errors.client ? errors.client : "Este campo es obligatorio *"
            }
          />
          <CssTextField
            id="email"
            placeholder="Correo electronico"
            label="Correo electronico"
            name="email"
            value={values.email}
            onChange={handleChange}
            error={errors.email && true}
            helperText={
              errors.email ? errors.email : "Este campo es obligatorio *"
            }
          />
          <CssTextField
            id="additional"
            label="Adicionales"
            multiline
            rows={4}
            name="additional"
            value={values.additional}
            onChange={handleChange}
            error={errors.additional && true}
            helperText={
              errors.additional
                ? errors.additional
                : "Este campo es obligatorio *"
            }
          />
          <MainButton buttonName="Enviar" icon={<SendIcon />} type="submit" />
        </div>
      </Box>
    </div>
  );
};

const FifthSection = () => {
  const [isSubmitted, setIsSubmitted] = useState(false);
  function submitForm() {
    setIsSubmitted(true);
  }
  return (
    <div className="fifth-section">
      <Container>
        {!isSubmitted && (
          <MainTitle
            textContent="Contactanos"
            description="Praesent dapibus eleifend mi, at fringilla diam. Integer a hendrerit magna. Nulla hendrerit turpis vel mauris interdum pretium."
          />
        )}
        {!isSubmitted ? (
          <ContactForm submitForm={submitForm} />
        ) : (
          <ContactFormSuccess />
        )}
      </Container>
    </div>
  );
};

export default FifthSection;
