import React from 'react';
import { Button } from '@mui/material';


const stylesButton = {
    backgroundImage: 'linear-gradient(to right, #FF8008 10%, #FFC837  50%, #FF8008  100%)',
    width:'100%',
    height:'50px',
    color: 'white',
    fontSize: '1em',
    transition: '0.5s',
    '&:hover': {
        filter: 'brightness(1.23)'
    }
    
}

const MainButton = (props) => {
    return (
        <div className="buttonContent">
            <Button onClick={props.onclick} type={props.type} sx={stylesButton} variant="contained" endIcon={props.icon}>
                {props.buttonName}
            </Button>
        </div>
    );
};

export default MainButton;